const modifyCityName = (name) => {
    let arr = name.split('');
    let lastLiteral = arr.splice(-1, 1);
    switch(lastLiteral.join('')) {
        case '': 
            // arr.splice(-2, 2);
            let secondLiteral = arr.splice(-1, 1);
            (secondLiteral[0] === '' || secondLiteral[0] === '') ? arr.push(''): arr.push('');
            return arr.join('');
        case '': 
            arr.push('');
            return arr.join('');
        case '':
            arr.push(lastLiteral);
            return arr.join('');
        case '':
            arr.push(lastLiteral);
            return arr.join('');
        case '': 
            arr.push('');
            return arr.join('');
        case '': 
            arr.push(lastLiteral);
            return arr.join('');
        default: 
            arr.push(lastLiteral, 'e');
            return arr.join('')
    } 
}
export default modifyCityName;