import { Link } from "react-router-dom";

const Page404 = ({defPage}) => {
    console.log('page404');
    return (
        <div className="error-404">
            <p style={{'textAlign': 'center', 'fontSize': '24px', 'marginBottom': '30px', 'color': 'blue'}}>,      </p>
            <Link style={{'display': 'block', 'textAlign': 'center', 'fontSize': '18px'}} to={defPage}>   </Link>
        </div>
    )
};
export default Page404;