const _changeWeek = (actualDate, iterator) => {
    const weekDays = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
    };
    return weekDays[(actualDate.getDay() + iterator) > 6 ? (actualDate.getDay() + iterator) - 7: (actualDate.getDay() + iterator)];
}

export default _changeWeek;